import {
  CSSObject,
  Anchor as MantineAnchor,
  AnchorProps as MantineAnchorProps,
  MantineTheme,
} from '@mantine/core';
import { FC, PropsWithChildren, forwardRef } from 'react';
import {
  EventHandler,
  FormattedColor,
  TwBaseProps,
  TwPolymorphicComponent,
} from '../../types';
import { styled } from '../../utils';

interface BaseAnchorProps
  extends TwBaseProps,
    PropsWithChildren,
    TwPolymorphicComponent,
    Omit<EventHandler<React.AnchorHTMLAttributes<HTMLAnchorElement>>, 'onClick'>,
    Pick<
      MantineAnchorProps,
      | 'align'
      | 'weight'
      | 'transform'
      | 'align'
      | 'lineClamp'
      | 'truncate'
      | 'inline'
      | 'underline'
      | 'strikethrough'
      | 'italic'
      | 'size'
    > {
  color?: FormattedColor;
}

export interface AnchorDefaultProps
  extends BaseAnchorProps,
    Pick<React.AnchorHTMLAttributes<HTMLAnchorElement>, 'href' | 'target'> {
  as?: 'a';
}

export interface AnchorButtonProps extends BaseAnchorProps {
  as: 'button';
  onClick?: () => any;
  disabled?: boolean;
}

export type AnchorProps = AnchorDefaultProps | AnchorButtonProps;

const Root = styled(MantineAnchor)<MantineAnchorProps>(() => ({}));

export const Anchor: FC<AnchorProps> = forwardRef((props, ref) => {
  const { as = 'a', color = 'one.5' } = props;
  const isDisabledButton = as === 'button' && 'disabled' in props && !props.disabled;

  // TODO: Extract to class
  const getCustomStyles = (theme: MantineTheme): CSSObject => {
    const computedColor = (() => {
      const [clr, shade] = color.split('.');
      return theme.colors[clr][+shade];
    })();

    if (isDisabledButton) {
      return {
        color: theme.colors.gray[3],
      };
    }

    return {
      ':hover': {
        color: theme.fn.darken(computedColor, 0.075),
      },
    };
  };

  const actions = (() => {
    if (isDisabledButton)
      return {
        onClick: () => false,
      };
    return {};
  })();

  return (
    <Root
      ref={ref}
      {...{
        ...props,
        ...actions,
        as,
        color,
        sx: (theme: MantineTheme) => getCustomStyles(theme),
      }}
    />
  );
});
